<template>
  <v-container fluid fill-height>
    <v-layout align-center justify-center>
      <v-flex xs12 sm8 md4>
        <the-login-box @submit="submit"/>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import TheLoginBox from "../components/misc/TheLoginBox";
import { mapActions } from "vuex";
import { notificationModule } from "@/store/modules";

export default {
  components: {
    TheLoginBox
  },
  mounted() {
    this.$store.dispatch("auth/checkIfAnyUserExists");
  },
  methods: {
    async submit(data) {
      try {
        const response = await this.authenticate(data);
        this.$router.push(
          this.$route.query.redirect ? this.$route.query.redirect : "/"
        );
      } catch (err) {
        console.log(err); // TOD: logger
      }
    },
    ...mapActions({
      authenticate: "auth/authenticate"
    })
  },
  computed: {
    anyUserExists() {
      return this.$store.getters["auth/anyUserExists"];
    }
  },
  watch: {
    anyUserExists: {
      immediate: true,
      handler(anyUserExists) {
        if (!anyUserExists) {
          this.$router.replace("/setup");
        }
      }
    }
  }
};
</script>
